body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar_button{
  text-decoration: none;
  color:white;
  text-transform: capitalize;
}

.navbar_button:hover {
  color: #3498db !important;
} 

.menu-active {
  font-weight: bold;
  border-bottom: 0.01rem solid #f2f2f8;
}

.btn-default {
  border: 0.1rem solid rgb(114, 114, 226);
  background-color: rgb(248, 245, 240);
  border-radius: 0.4rem;
  padding: 0.2rem;
  text-decoration: none;
  transition: 0.3s;
  display: inline-block;
}

.btn-default:hover {
  color: white;
  background-color: #3498db;
}

.brand-name {
  color: #565387;
}

.note {
  font-weight: 300;
}

.animated {
  width: 50rem;
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

#header {
  width: 100%;
  height: 80vh;
  margin-top: 0.5rem;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

.footer_class{
  clear: both;
    position: static;
    height: 50rem;
    margin-bottom: 0rem;
}

.inputData{
  width: 50%;
  margin-top: 1%;
}
